.programs__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: $desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.programs__item {
  position: relative;
  background-color: #f2f2f2;
  margin: 0 auto;
  width: 280px;
  padding: 29px 20px 0;
  margin-top: 20px;

  @media (min-width: $tablet-width) {
    width: 708px;
    padding: 77px 53px 39px;
  }

  @media (min-width: $desktop-width) {
    width: 570px;
    margin: 0;
  }

  &::before {
    content: "";
    position: absolute;
    left: 22px;
    top: 24px;
    width: 50px;
    height: 50px;
    background-color: #68b738;
    border-radius: 50%;

    @media (min-width: $tablet-width) {
      width: 200px;
      height: 200px;
      left: auto;
      right: 62px;
      top: 90px;
    }

    @media (min-width: $desktop-width) {
      left: 45px;
      top: 40px;
      width: 100px;
      height: 100px;
    }
  }

  &::after {
    content: "";
    position: absolute;
  }
}

.programs__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
  padding-left: 70px;

  @media (min-width: $tablet-width) {
    font-size: 36px;
    line-height: 100%;
    padding-left: 0;
  }

  @media (min-width: $desktop-width) {
    margin-left: 125px;
  }
}

.programs__item--slimming {
  &::after {
    content: "";
    background-image: url("../img/programs-slimming.svg");
    background-repeat: no-repeat;
    left: 30px;
    top: 22px;
    width: 35px;
    height: 49px;

    @media (min-width: $tablet-width) {
      width: 141px;
      height: 194px;
      right: 92px;
      background-size: 141px 194px;
      left: auto;
      top: 80px;
    }

    @media (min-width: $desktop-width) {
      background-size: 71px 97px;
      width: 71px;
      height: 97px;
      left: 60px;
      top: 36px;
    }
  }
}

.programs__item--muscle-building {
  &::after {
    content: "";
    background-image: url("../img/programs-muscle-buildin.svg");
    background-repeat: no-repeat;
    left: 13px;
    top: 35px;
    width: 67px;
    height: 28px;

    @media (min-width: $tablet-width) {
      width: 268px;
      height: 113px;
      right: 92px;
      top: 130px;
      background-size: 268px 113px;
      left: 410px;
    }

    @media (min-width: $desktop-width) {
      background-size: 134px 57px;
      width: 134px;
      height: 57px;
      left: 26px;
      top: 57px;
    }
  }
}

.programs__text {
  margin: 0;
  max-width: 240px;
  padding-bottom: 21px;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-width) {
    max-width: 340px;
    font-size: 16px;
    line-height: 24px;
    border-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    max-width: 457px;
    margin-top: 73px;
  }
}

.programs__link {
  position: relative;
  display: block;
  padding-top: 15px;
  padding-bottom: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 100%;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;

  &::after {
    content: "";
    position: absolute;
    right: 22px;
    top: 50%;
    width: 24px;
    height: 12px;
    background: url("../img/stretch-arrow.svg");
    background-repeat: no-repeat;
    background-position: right;
    transform: translateY(-50%);
    transition: 0.25s ease-in-out;
    background-size: cover;
  }

  &:hover::after {
    right: 14px;
    width: 32px;
  }

  &:active {
    opacity: 0.3;
    transition: 0.25s ease-in-out;
  }

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 30px;
  }
}

.programs__link--slimming {
  width: 147px;

  @media (min-width: $tablet-width) {
    width: 172px;
  }
}

.programs__link--pro {
  width: 143px;

  @media (min-width: $tablet-width) {
    width: 168px;
  }
}
