.socials {
  @media (min-width: $tablet-width) {
    display: flex;
  }
}

.socials__list {
  list-style-type: none;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: $tablet-width) {
    border: 0;
  }
}

.socials__item {
  min-width: 50px;
  min-height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}


.socials__icon {
  color: #666666;

  &:hover {
    color: #68b738;
    transition: 0.25s ease-in-out;
  }

  &:active {
    opacity: 0.3;
    transition: 0.25s ease-in-out;
  }
}
